.info {
    margin-top: 2rem;
    &__container {
        margin: 0 auto;
        max-width: 60rem;
        padding-bottom: 2rem;
        border-bottom: 1px solid #dedede;
    }
    &__users-descr {
        width: 50%;
        text-align: justify;
    }
    &__users-descr__wrapper {
        margin-top: 1rem;
    }
    &__users-descr__pic {
        float: left;
        shape-outside: circle(50%);
        margin-right: 1rem;
    }
    &__users-descr__text {
        line-height: 1.2;
    }
    &__users-list {
        max-width: 30rem;
        padding-left: 2rem;
    }
    &__users-list__title {
        margin-left: 3rem;
    }
    &__users-list__tabs {
        font-size: 0;
        width: 22rem;
        margin: 0 auto;
        margin-top: 1.5rem;
    }
  
    &__users-list__tabs>input[type="radio"] {
        display: none;

    }
  
    &__users-list__tabs>div {
        display: none;
        border: 1px solid #e0e0e0;
        padding: 0.5rem  1rem;
        font-size: 1rem;
    }
    #tab-btn-1:checked~#content-1,
    #tab-btn-2:checked~#content-2 {
        display: flex;
        flex-direction: column;
        box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.6);
    }
  
    &__users-list__tabs>label {
        display: inline-block;
        text-align: center;
        vertical-align: middle;
        user-select: none;
        background-color: #f5f5f5;
        border: 1px solid #e0e0e0;
        border-radius: 1rem 1rem 0px 0px;
        padding: 3px 10px 3px 8px;
        font-size: 1rem;
        line-height: 1.5;
        transition: all 0.15s ease-in-out, background-color 0.15s ease-in-out;
        cursor: pointer;
        position: relative;
        top: 1px;
    }
  
    &__users-list__tabs>label:not(:first-of-type) {
        border-left: none;
    }
  
    &__users-list__tabs>input[type="radio"]:checked+label {
        background-color: #fff;
        border-bottom: 1px solid #fff;
    }
    &__users-list__container {
        width: 100%;
    }
    &__users-list__cell {
        width: 12rem;
    }
    &__users-list__bar {
        position: relative;
        width: 100%;
        height: 1.1rem;
        margin-top: 0.5rem;
        text-align: center;
        border: solid 1px rgba(0, 0, 0, 0.162);
        border-radius: 3px;
        background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
        &::after {
            content: '';
            position: absolute;
            width: 60%;
            height: 0.9rem;
            top: 0.5px;
            left: 0;
            z-index: 1;
            border-radius: 3px;
            background-color: rgb(255, 145, 49);
        }
        span {
            position: relative;
            top: -0.2rem;
            font-size: 0.8rem;
            z-index: 2;
        }
    }
    &__users-list__steps {
        margin-top: 1.5rem;
        font-size: 0.6rem;
    }
    &__users-list__step {
        padding: 0.3rem 0.6rem;
        width: 2.85rem;
        cursor: pointer;
        &:nth-child(1) {
            position: relative;
            border: solid 1px #dedede;
            border-radius: 4px;
            background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
            clip-path: polygon(80% 0, 100% 50%, 80% 100%, 0 100%, 0 0);
        }
        &:nth-child(2) {
            position: relative;
            left: -9px;
            border: solid 1px #dedede;
            border-radius: 4px;
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
            clip-path: polygon(0 0, 80% 0%, 100% 50%, 80% 100%, 0 100%, 20% 50%);
        }
        &:nth-child(3) {
            position: relative;
            left: -18px;
            border: solid 1px #dedede;
            border-radius: 4px;
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
            clip-path: polygon(0 0, 80% 0%, 100% 50%, 80% 100%, 0 100%, 20% 50%);
        }
        &:nth-child(4) {
            position: relative;
            left: -27px;
            border: solid 1px #dedede;
            border-radius: 4px;
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
            clip-path: polygon(0 0, 80% 0%, 100% 50%, 80% 100%, 0 100%, 20% 50%);
        }
        &:nth-child(5) {
            position: relative;
            left: -36px;
            border: solid 1px #dedede;
            border-radius: 4px;
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            background-image: linear-gradient( 90deg, rgb(230,230,230) 0%, rgb(245,250,251) 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 20% 50%);
        }
    }
    .info__users-list__slider {
        width: 100%;
        margin-bottom: 0.5rem;
    }
    .irs-min, .irs-max, .irs-from, .irs-to, .irs-single {
        display: none;
    }
    .irs--flat .irs-bar {
        top: 25px;
        height: 12px;
        background-color: rgb(255, 145, 49);
    }
    .irs-handle {
        cursor: pointer;
        &::after {
            content: url('../img/info/slider__btn.png');
            position: relative;
            width: 1rem;
            height: 2rem;
            top: -4px;
            z-index: 1;
            pointer-events: none;
        }
    } 
    &__users-list__progress {
        width: 100%;
    }
    &__users-list__circle {
        position: relative;
    }
    &__users-list__stat {
        position: absolute;
    }
    &__users-list__stars {
        margin-top: 0.5rem;
    }
    .cls-5 {
        cursor: pointer;
        stroke: #000;
        stroke-linejoin: round;
        stroke-opacity: 0.21;
        stroke-width: 1px;
        fill-rule: evenodd;
        filter: url(#filter_1); 
    }
    &__succeed {
        width: 100%;
        margin-top: 1rem;
    }
    &__succeed-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        margin-top: 0.5rem;
    }
    &__succeed-item {
        display: flex;
        flex-direction: column;
        width: 18rem;
    }
    &__succeed-text {
        p {
            margin-top: 1rem;
            span {
                a {
                    color: rgb(255, 145, 49);
                }
            }
        }
    }
    &__succeed-text__wrapper {
        margin-top: 1rem;
        border-left: 2px solid rgb(255, 145, 49);
        &:nth-child(1) {
            margin-top: 0;
            margin-bottom: 1rem;
        }
    }
    &__succeed-text__color {
        font-size: 0.8rem;
        color: rgb(255, 145, 49);
        margin-left: 1rem;
        &:nth-child(2) {
            position: relative;
            color: rgb(78, 78, 78);
            margin: 1.5rem;
            &::after {
                content: '"';
                font-size: 6rem;
                position: absolute;
                opacity: 0.3;
                right: -1.5rem;
                top: 5rem;
            }
            &::before {
                content: '"';
                font-size: 6rem;
                position: absolute;
                opacity: 0.3;
                left: -2.5rem;
                top: -2.5rem;
            }
        }
    }
    &__succeed-text__link {
        a {
            float: right;
            color: rgb(255, 145, 49);
        }
    }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
    .info {
        &__users {
            padding: 0 1rem;
        }
        &__users-descr__photo {
            width: 8rem;
            height: 8rem;
        }
        &__succeed {
            padding: 0 1rem;
        }
        &__succeed-container {
            justify-content: center;
        }
        &__succeed-title {
            margin: 0 auto;
        }
        &__succeed-item {
            padding: 1rem;
            margin: 1rem;
            border-radius: 0.5rem;
            width: 18rem;
            box-shadow: 8px 7px 14px -1px rgba(0,0,0,0.23);
        }
    }
}

@media screen and (min-width: 300px) and (max-width: 767.5px) {
    .info {
        &__users {
            flex-direction: column-reverse;
            padding: 0;
        }
        &__users-list__title {
            margin: 0 auto;
        }
        &__users-list {
            padding: 0 0.5rem;
            margin: 0 auto;
        }
        &__users-descr {
            width: 100%;
            padding: 0 1rem;
            margin-top: 4rem;
        }
        &__users-descr__title {
            text-align: center;
        }
        &__users-descr__photo {
            width: 8rem;
            height: 8rem;
        }
        &__succeed {
            padding: 0 1rem;
        }
        &__succeed-container {
            justify-content: center;
        }
        &__succeed-title {
            margin: 0 auto;
            text-align: center;
        }
        &__succeed-item {
            padding: 1rem;
            margin: 1rem;
            border-radius: 0.5rem;
            width: 18rem;
            box-shadow: 8px 7px 14px -1px rgba(0,0,0,0.23);
        }
        &__users-list__tabs {
            width: 19rem;
        }
        &__users-list__tabs>div {
            padding: 0.5rem  0.2rem;
        }
    }
}